*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Roboto", sans-serif;
}

.bald-text {
  font-weight: 700;
}

.container {
  height: 100vh;
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  padding: 1rem;
}
.container__photo {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_630.jpg");
}
.container__textarea {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
}
.container__title {
  font-size: 42px;
}
.container__text {
  line-height: 24px;
  font-size: 17px;
  text-align: justify;
}
.container__btn {
  font-size: 16px;
  background-color: #e4c1ae;
  color: #ffffff;
  padding: 16px 40px;
  border-radius: 32px;
  border-style: none;
  cursor: pointer;
  align-self: center;
}
.container__btn:hover {
  background-color: rgba(228, 193, 174, 0.7254901961);
}

.modal {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  overflow: hidden;
}
.modal__content {
  width: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  text-align: center;
  padding: 50px;
  background-color: #ffffff;
  gap: 1rem;
}
.modal__close {
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 42px;
  transform: rotate(45deg);
  cursor: pointer;
  color: #333;
}
.modal__title {
  font-size: 42px;
  padding-bottom: 24px;
}
.modal__btn {
  font-size: 16px;
  background-color: #e4c1ae;
  color: #ffffff;
  padding: 16px 40px;
  border-radius: 32px;
  border-style: none;
  cursor: pointer;
  display: none;
  padding: 10px 20px;
}
.modal__btn:hover {
  background-color: rgba(228, 193, 174, 0.7254901961);
}

@media (min-width: 576px) {
  .container__photo {
    background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_767.jpg");
  }
}
@media (min-width: 768px) {
  .container__photo {
    background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  }
}
@media (min-width: 992px) {
  .container {
    margin: auto 10rem;
    height: 60vh;
    flex-direction: row;
    padding: 5rem;
  }
  .container__photo {
    background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1002.jpg");
  }
  .container__textarea {
    width: 100vh;
  }
  .container__text {
    font-size: 16px;
  }
  .container__btn {
    align-self: flex-start;
  }
}
@media (min-width: 1200px) {
  .container__photo {
    background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1234.jpg");
  }
}
@media (min-width: 1400px) {
  .container__photo {
    background-image: url("../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1400.jpg");
  }
}